.poke-card {
  width: 100%;
  height: 270px;
  margin: 0  5px 20px;
  border-radius: 30px;
  text-align: center;
  flex: 1 1 auto;
  padding: 20px 5px 5px;
  cursor: pointer;
  transition: all .3s ease;
  box-shadow: 0 10px 20px -1px #000000;
}

.poke-name {
  font-size: 24px;
  margin-top: 20px;
}

.poke-name::first-letter {
  text-transform: uppercase;
}

.poke-image {
  height: 50%;
  width: 80%;
}

.poke-type {
  display: inline-block;
  margin: 20px 0;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 16px;
  padding: 5px;
  box-shadow: 0 3px 10px 1px #000;
  border-radius: 5px;
}

@media screen and (min-width: 360px) {
  .poke-card {
    max-width: 150px;
    margin: 10px;
  }
}
@media screen and (min-width: 524px) {
  .poke-card {
    max-width: 210px;
    /* height: 300px; */
  }
}

@media screen and (min-width: 669px) {
  .poke-card {
    max-width: 230px;
    height: 320px;
    margin: 0  15px 40px;
  }
  .poke-card:hover {
    transform: scale(1.05);
  }
  .poke-name:first-letter {
    text-transform:capitalize;
  }
}

@media screen and (orientation: landscape) and (max-width: 900px) {
  .poke-card {
    max-width: 220px;
    height: 260px;
    margin: 0  15px 30px;
  }
}





